<template>
  <div class="page has-navbar" v-nav="{hideNavbar:true}">
    <div class="navbar top" style="visibility: visible">
      <div style="position: absolute;width: 100%;height: 100%;visibility: visible;">
        <von-header>
          <button class="button button-icon ion-ios-arrow-back" slot="left" @click="$router.go(-1)"></button>
          <span slot="title">定单产品明细</span>
          <span slot="right" class="right-menu" v-show="action==='check'&&action!=='checkOnly'" @click="onMenuClick">编辑</span>
          <span slot="right" class="right-menu" v-show="action ==='edit'" @click="action='check'">取消</span>
          <span slot="right" class="right-menu" v-show="action ==='new'" @click="associate">
              <!--关联报价单-->
            </span>
        </von-header>
      </div>

    </div>
    <div class="page-content compute-detail" style="padding-bottom: 50px">
      <cst-item label="产品名称"  v-model="cst_order_line.cst_prod_name" editType="text" placeholder="输入产品名称"  :action="action" :canEdit="true"></cst-item>
      <cst-item
        label="产品类型"
        v-model="cst_order_line.type_name"
        editType="text"
        placeholder="输入产品类型"
        :action="action"
        :canEdit="true"
        :hasArrow="true"
        @click.native="chooseType('prodType')"></cst-item>
      <cst-item label="客户模号"  v-model="cst_order_line.cst_mold_no" placeholder="输入客户模号"  :action="action" :canEdit="true"></cst-item>
      <cst-item @click.native="prodsClick" label="客户制品信息"
                editType="text"
                :action="action"
                :canEdit="false"
                :hasArrow="true"
                :arrowText="cst_order_line.prod_list.length+''">

      </cst-item>
      <cst-item label="制品材料" v-model="cst_order_line.material"
                editType="text"
                :action="action"
                :hasArrow="true"
                :canEdit="false"
                @click.native="chooseType('material')">
      </cst-item>
      <!--<cst-item label="腔/芯材料"  v-model="cst_order_line.cavity_material"-->
      <!--editType="text"-->
      <!--:action="action"-->
      <!--:hasArrow="true"-->
      <!--arrowText="修改"-->
      <!--:canEdit="false"-->
      <!--@click.native="chooseType('cavity_material')">-->
      <!--</cst-item>-->
      <cst-item label="腔/芯材料"  placeholder="请输入腔/芯材料" v-model="cst_order_line.cavity_material" editType="text" :action="action" :canEdit="true"></cst-item>
      <cst-item label="单位" placeholder="请输入单位" v-model="cst_order_line.unit_name" editType="text" :action="action" :canEdit="true"></cst-item>
      <!--<cst-item label="单位"  v-model="cst_order_line.unit"-->
      <!--editType="text"-->
      <!--:action="action"-->
      <!--:hasArrow="true"-->
      <!--arrowText="修改"-->
      <!--:canEdit="false"-->
      <!--@click.native="chooseType('unit')">-->
      <!--</cst-item>-->
      <cst-item v-if="action==='check'||action==='checkOnly'||action==='approve'" label="正式开模日期"  v-model="cst_order_line.start_date" :action="action" :canEdit="false"></cst-item>
      <datepicker v-else v-model="cst_order_line.start_date" label="正式开模日期" date-format="yyyy-mm-dd"></datepicker>
      <cst-item v-if="action==='check'||action==='checkOnly'||action==='approve'" label="首次试模日期"  v-model="cst_order_line.first_try_date"  :action="action" :canEdit="false"></cst-item>
      <datepicker v-else v-model="cst_order_line.first_try_date" label="首次试模日期" date-format="yyyy-mm-dd"></datepicker>
      <cst-item v-if="action==='check'||action==='checkOnly'||action==='approve'" label="交货日期"  v-model="cst_order_line.delivery_date"  :action="action" :canEdit="false"></cst-item>
      <datepicker v-else v-model="cst_order_line.delivery_date" label="交货日期" date-format="yyyy-mm-dd"></datepicker>
      <cst-item label="单价"  v-model="cst_order_line.price" editType="number" placeholder="0"  :action="action" :canEdit="action==='new'||action==='edit'"></cst-item>
      <cst-item label="数量"  v-model="cst_order_line.count" editType="number" placeholder="0"   :action="action" :canEdit="action==='new'||action==='edit'"></cst-item>
      <cst-item label="金额"  v-model="newPrice" editType="number" :action="action" :canEdit="false"></cst-item>

      <cst-item label="备注"  placeholder="请输入备注"  v-model="cst_order_line.remark" editType="text" :action="action" :canEdit="action==='new'||action==='edit'"></cst-item>
    </div>
    <sidebar-check :showFilter="showFilter" :dicType="dicType" :chosen="chosen" v-on:hideMask="showFilter = false"></sidebar-check>
    <div class="btns" v-show="action!='check'&&action!=='checkOnly'&&action!=='approve'">
      <div class="btn" @click="saveData">
        保存
      </div>
    </div>
  </div>
</template>
<script>
  import  cstItem  from '@/components/cstItem'
  import { todayDate } from '@/utils'
  import { queryLineDetail ,modifyLine,associateQuote } from '@/api/customerOrder/order'
  import sess from '@/utils/sess'
  import {mapGetters} from 'vuex'
  import SwipeItem from "../../../components/swipeItem";
  import SidebarCheck from "../../../components/sidebarCheck";
  export default{
    name:'orderLineInfo',
    components:{
      SidebarCheck,
      SwipeItem,
      cstItem
    },
    data(){
      return{
        action:'',
        tabs:['头信息','明细信息','付款明细','备注'],
        tabIndex:0,
        cst_order_line:{//this.cst_order_line.delivery_date
          cavity_material:'',
          count:'',
          cst_mold_no:'',
          delivery_date:todayDate(),
          first_try_date:todayDate(),
          start_date:todayDate(),
          material:'请选择',
          order_line_id:'',
          price:'',
          remark:'',
          prod_list:[],
          type:0,
          type_name:'请选择',
          unit:'',
          unit_name:'',
          up:'0',
          project_no:'',
          cst_prod_name:''
        },
        order_line_id:'',
        project_id:'',
        showFilter:false,
        dicType:''
      }
    },
    computed:{
      ...mapGetters(['userinfo']),
      newPrice(){
        let Price=Number(this.cst_order_line.count)*Number(this.cst_order_line.price)
        this.cst_order_line.up=Price
        return Price
      }
    },
    created(){
      this.action = this.$route.params.action
      if(this.action ==='new'){
        //如果是新增，则将报价单id带过来
        this.project_id = this.$route.params.line_id

      }else{
        this.order_line_id = this.$route.params.line_id
        this.getData();
      }
    },
    activated(){
      //从制品列表返回
      const prods = sess.get('prods');
      if(prods&&prods!=''){
        this.cst_order_line.prod_list = JSON.parse(prods)
        sess.remove('prods')
        return
      }
      //从历史报价单列表返回
      const historyQuote = sess.get('historyQuote');
      if(historyQuote&&historyQuote!=''){
        this.project_id = historyQuote.project_id
        sess.remove('prods')
        return
      }
    },
    destoryed(){
      console.log('destoryed')
    },
    methods:{

      //制品信息  列表点击
      prodsClick(){
//        if(this.action==='checkOnly'){
//          this.$router.forward('/customerOrder/order/prdList/checkOnly/undefined')
//          return
//        }
        if(this.cst_order_line.prod_list.length===0){
          this.$router.forward('/customerOrder/order/prdList/'+this.action+'/undefined')
        }else{
          window.sessionStorage.setItem('prdList', JSON.stringify(this.cst_order_line.prod_list))
          //this.$router.forward('/customerOrder/order/prdList/'+this.action+'/'+JSON.stringify(this.cst_order_line.prod_list))
          this.$router.forward('/customerOrder/order/prdList/'+this.action+'/'+this.cst_order_line.order_line_id)
        }
      },
      chooseType(type){
        if(this.action==='check'||this.action==='checkOnly'||this.action==='approve'){
          return
        }
        this.dicType = type
        this.showFilter = true
      },
      chosen(item){
        switch (this.dicType){
          case 'material':{
            this.cst_order_line.material = item.raw_nature
            break
          }
          case 'unit':{
            this.cst_order_line.unit = item.unit;
            this.cst_order_line.unit_name = item.unit_name;
            break
          }
          case 'cavity_material':{
            this.cst_order_line.cavity_material = item.name;
            break
          }
          case 'type':{
            this.cst_order_line.type = item.type
            this.cst_order_line.type_name = item.type_name
            break
          }
          case 'prodType':{
            this.cst_order_line.type = item.type
            this.cst_order_line.type_name = item.type_name
            break
          }
        }
      },
      associate(){
        //关联报价单/核算单
        this.$router.forward('/customerOrder/quote/quoteHistory/associate/'+this.project_id)

      },
      onMenuClick(){
        if(this.action==='check'){
          this.action = 'edit'
        }else{
          this.action = 'check'
        }
      },
      getData(){
        const that = this;
        const requestData = {
          order_line_id:that.order_line_id
        }
        queryLineDetail(requestData).then(res=>{
          that.cst_order_line = res
          this.cst_order_line.project_no = sess.get('projectNo')
          sess.remove('projectNo')
        }).catch(error=>{
          $toast.show(error.description)
        })
      },
      saveData(){
        if(this.cst_order_line.cst_prod_name.replace(/\s+/g,"")===''){
          $toast.show('请输入产品名称')
          return
        }
        if(this.cst_order_line.type_name==='请选择'){
          $toast.show('请选择产品类型')
          return
        }
        if(this.cst_order_line.cst_mold_no.replace(/\s+/g,"")===''){
          $toast.show('请输入客户模号')
          return
        }

        if(this.cst_order_line.prod_list.length<1){
          $toast.show('请添加客户制品信息')
          return
        }
        if(this.cst_order_line.material==='请选择'||this.cst_order_line.material===undefined){
          $toast.show('请选择制品材料')
          return
        }
        if(this.cst_order_line.cavity_material.replace(/\s+/g,"")===''){
          $toast.show('请输入腔/芯材料')
          return
        }
        if(this.cst_order_line.unit_name.replace(/\s+/g,"")===''){
          $toast.show('请输入单位')
          return
        }
        if(this.cst_order_line.up=='0'){
          $toast.show('请输入单价及数量')
          return
        }

        const requestData = {
          project_id:this.project_id,
          cst_order_line:this.cst_order_line
        }
        modifyLine(requestData).then(res=>{
          console.log(res)
          this.action = 'check'
          $toast.show('保存成功')
        }).catch(error=>{
          $toast.show(error.description)
        })
      }
    },
  }
</script>
<style lang="scss" scoped>
  .compute-item .note{width: 100px}
</style>



